.va-color-input {
  display: flex;
  align-items: center !important;
}
.va-color-input .form-group {
  margin-bottom: 0;
}
.va-color-input__input {
  margin-bottom: 0;
  min-width: 5.6rem;
}
.va-color-input__input__pointer {
  cursor: pointer;
}
.va-color-input__hidden-input {
  visibility: hidden;
  width: 0;
  height: 0;
  overflow: hidden;
  position: absolute;
  pointer-events: none;
}