    }

    & .otis-dashboard__setting-group {
      flex-flow: wrap;
      flex-grow: 1;
      width: 100%;
      display: flex;
    }

    & .otis-dashboard__setting {
      box-sizing: border-box;
      flex-grow: 1;
      padding: 10px 10px 10px 0;
      display: inline-block;

      &.otis-dashboard__setting--full-width {
        width: 100%;
      }

      & label {
        margin: 10px 0 4px;
        font-size: 13px;
        line-height: 1.4;
        display: block;
      }

      & input[type="text"] {
        display: block;
      }

      & button, & .button {
        margin-top: 15px;
      }

      & button {
        display: inline-block;
      }

      & table {
        width: 100%;

        & tr:nth-child(2n) {
          background-color: #f9f9f9;
        }

        & th:first-child {
          text-align: left;
        }
      }

      & .otis-dashboard__poi-counts {
        & th:last-child {
          text-align: right;
        }

        & td:first-child {
          text-transform: capitalize;
        }

        & td:last-child {
          text-align: right;
        }
      }
    }

