    & .otis-dashboard__status .postbox, & .otis-dashboard__initial-import .postbox, & .otis-dashboard__settings .postbox {
      box-sizing: border-box;
      min-width: auto;
      margin-bottom: 0;
      padding: 10px;
      transition: background-color .25s;
      position: relative;
    }

    & .otis-dashboard__status .postbox.success, & .otis-dashboard__initial-import .postbox.success, & .otis-dashboard__settings .postbox.success {
      background-color: #28834c;
    }

    & .vdpr-datepicker__calendar-input-wrapper {
      display: none;
    }

    & .otis-dashboard__settings {
      padding: 10px 0;
    }

    & .otis-dashboard__import-log td:last-child, & .otis-dashboard__import-log th:last-child {
      text-align: left;
    }

