      }
    }

    & .otis-ellipsis {
      width: 80px;
      height: 80px;
      display: inline-block;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

      & div {
        background: #28834c;
        border-radius: 50%;
        width: 13px;
        height: 13px;
        animation-timing-function: cubic-bezier(0, 1, 1, 0);
        position: absolute;
        top: 33px;

        &:first-child {
          animation: .6s infinite otis-ellipsis1;
