        }

        &:nth-child(2) {
          animation: .6s infinite otis-ellipsis2;
          left: 8px;
        }

        &:nth-child(3) {
          animation: .6s infinite otis-ellipsis2;
          left: 32px;
        }

        &:nth-child(4) {
          animation: .6s infinite otis-ellipsis3;
          left: 56px;
        }
      }
    }

    & input[type="text"], & input[type="password"] {
      padding: 0;

      &:focus {
        box-shadow: none;
        outline: none;
      }
    }

    & .otis-dashboard__fieldset {
      flex-direction: column;
      display: flex;
    }

    & .otis-dashboard__form-grid {
      flex-flow: wrap;
      gap: 2rem;
      display: flex;
    }

    & .otis-dashboard__description {
      opacity: .8;
    }
  }
}

@keyframes fade-up {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes otis-ellipsis1 {
  0% {
    transform: scale(0);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes otis-ellipsis3 {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(0);
  }
}

@keyframes otis-ellipsis2 {
  0% {
    transform: translate(0);
  }

  100% {
    transform: translate(24px);
  }
}
